/*
* Colors
*/
$base-color: #6372ff;

/*
* Media queries helper
*/
$breakpoint-mobile: 768px;
#ac {
  display: flex;

  width: 290px;
  height: 34px;
  border-radius: 3px;
  background-color: #f1f1f1;
  margin-bottom: 10px;

  background-image: url(../../assets/images/user.svg);
  background-repeat: no-repeat;
  background-position: 10px center;
  padding-left: 40px;
  font-size: 15px;
  background-size: 23px;
  color: black;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
}
/*
* Classes (ampersand + BEM)
*/
.login {
  display: flex;
  height: 100%;

  &__welcome-back {
    z-index: 1;
    transition: visibility 0.3s ease-in-out;
    position: absolute;
    transition: all 0.8s ease-in-out;
    width: 30%;
    font-family: "Raleway", sans-serif;

    &--active {
      visibility: visible;
      transform: translateX(0%);
    }

    &--inactive {
      visibility: hidden;
      transform: translateX(-100%);
    }

    &__logo-container {
      padding: 15px;
      display: flex;
      flex-direction: row;
      align-items: center;
      color: white;
      font-weight: bold;

      &--image {
        width: 38px;
        height: 38px;
        margin-right: 4px;
      }

      @media only screen and(max-width: $breakpoint-mobile) {
        font-size: 0px;
        justify-content: center;
      }
    }

    &__main-container {
      margin-top: 20%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      color: white;

      &__text-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 60%;
        text-align: center;

        &--title {
          font-size: 30px;
          font-weight: bold;
        }

        &--secondary {
          margin-top: 12px;
          font-size: 13px;
          font-weight: lighter;
          font-family: "Raleway", sans-serif;
        }

        @media (max-width: $breakpoint-mobile) {
          display: none;
        }
      }

      &__button-container {
        margin-top: 40px;
        border: solid 2px white;
        width: 50%;
        height: 40px;
        border-radius: 30px;
        font-size: 18px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        @media only screen and (max-width: $breakpoint-mobile) {
          font-size: 3vw;
        }
      }
    }
  }
  .recovery-email-pwd{
  
  }
  .forget-pwd-button-div{
    padding-left: 22%;
    
  }
  .back-to-login{
    margin-top: 8px;
    text-align: center;
  }
  .input-email-recovery{
    background-color: rgb(235, 230, 230);
    margin-top: 5px;
    width: 250px;
    padding: 5px;
    padding-left: 40px;
  }
  .recovery-email-pwd p{
    text-align: center;
  }
  .forget-email{
    width: max-content;
    padding-left: 25px;
    padding-right: 25px;
  }
  .forgot-pwd-div{
    margin-top: -20px;
    text-align: center;
  }
  .forgot-password{
    font-size: medium;
    font-weight: 600;
    text-decoration: underline;
    cursor: pointer;
    color: #5ca9fb;

  }
  .forgot-password:hover{
    text-decoration: none;
    color: grey;
  }
  .invalid-div{
    margin-bottom: 10px;
    padding-top: 10px;
    text-align: center;
    border-radius: 2px;
    width: 100%;
    padding-bottom: 5px;
    background-color: red;
    color: white;
  }
  .success-div{
    margin-bottom: 20px;
    padding-top: 10px;
    text-align: center;
    border-radius: 2px;
    padding-bottom: 5px;
    width: 100%;
    background-color: green;
    color: white;
  }
 
  &__create-container {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: $base-color;
    font-weight: bold;
    font-size: 30px;
    z-index: 2;
    width: 70%;
    left: 30.5%;
    transition: all 0.8s ease-in-out;
    font-family: "Raleway", sans-serif;

    &--active {
      visibility: visible;
      transform: translateX(0%);
    }

    &--inactive {
      visibility: hidden;
      transform: translateX(100%);
    }

    &__social-container {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      margin-top: 5px;

      &--facebook-icon {
        width: 30px;
        height: 30px;
        cursor: pointer;
      }

      &--google-icon {
        width: 22px;
        height: 22px;
        margin-left: 10px;
        cursor: pointer;
      }

      &--linkedin-icon {
        width: 22px;
        height: 22px;
        margin-left: 14px;
        cursor: pointer;
      }
    }

    &--info-text {
      margin-top: 15px;
      color: #aeaeae;
      font-size: 14px;
    }

    &__form-container {
      margin-top: 3%;
      &__form {
        display: flex;
        flex-direction: column;
        align-items: center;

        input {
          width: 290px;
          height: 34px;
          border-radius: 3px;
          background-color: #f1f1f1;
          margin-bottom: 10px;
          color: #242424;

          @media only screen and (max-width: $breakpoint-mobile) {
            width: 50vw;
          }
        }

        &--name {
          background-image: url(../../assets/images/user.svg);
          background-repeat: no-repeat;
          background-position: 10px center;
          padding-left: 40px;
          font-size: 15px;
          background-size: 23px;
        }

        &--email {
          background-image: url(../../assets/images/mail.svg);
          background-repeat: no-repeat;
          background-position: 10px center;
          padding-left: 40px;
          font-size: 15px;
          background-size: 23px;
        }

        &--password {
          background-image: url(../../assets/images/password.svg);
          background-repeat: no-repeat;
          background-position: 10px center;
          padding-left: 40px;
          font-size: 15px;
          background-size: 23px;
        }

        &--submit {
          margin-top: 20px;
          color: white;
          background: linear-gradient(to right, #6372ff 0%, #5ca9fb 100%);
          // background-color: $base-color;
          border: solid 2px white;
          width: 50%;
          height: 40px;
          border-radius: 30px;
          font-size: 18px;
          cursor: pointer;
        }
      }
    }
  }

  &__login-container {
    position: fixed;
    transition: all 0.7s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #986060;
    font-weight: bold;
    font-size: 30px;
    z-index: 2;
    font-family: "Raleway", sans-serif;

    &__logo-container {
      padding: 15px;
      display: flex;
      flex-direction: row;
      align-items: center;
      color: $base-color;
      font-weight: bold;

      &--image {
        width: 38px;
        height: 38px;
        margin-right: 4px;
      }
    }

    &__main-container {
      color: $base-color;
      font-size: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      font-weight: bold;

      &__social-container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin-top: 5px;

        &--facebook-icon {
          width: 30px;
          height: 30px;
          cursor: pointer;
        }

        &--google-icon {
          width: 22px;
          height: 22px;
          margin-left: 10px;
          cursor: pointer;
        }

        &--linkedin-icon {
          width: 22px;
          height: 22px;
          margin-left: 14px;
          cursor: pointer;
        }
      }

      &--info-text {
        margin-top: 15px;
        color: #aeaeae;
        font-size: 14px;
      }

      &__form-container {
        margin-top: 3%;
        &__form {
          display: flex;
          flex-direction: column;
          align-items: center;

          input {
            width: 290px;
            height: 34px;
            border-radius: 3px;
            background-color: #f1f1f1;
            margin-bottom: 10px;
            color: #242424;
          }

          &--name {
            background-image: url(../../assets/images/user.svg);
            background-repeat: no-repeat;
            background-position: 10px center;
            padding-left: 40px;
            font-size: 15px;
            background-size: 23px;
          }

          &--email {
            background-image: url(../../assets/images/mail.svg);
            background-repeat: no-repeat;
            background-position: 10px center;
            padding-left: 40px;
            font-size: 15px;
            background-size: 23px;
          }

          &--password {
            background-image: url(../../assets/images/password.svg);
            background-repeat: no-repeat;
            background-position: 10px center;
            padding-left: 40px;
            font-size: 15px;
            background-size: 23px;
          }

          &--submit {
            margin-top: 20px;
            color: white;
            background: linear-gradient(to right, #6372ff 0%, #5ca9fb 100%);
            // background-color: $base-color;
            border: solid 2px white;
            width: 50%;
            height: 40px;
            border-radius: 30px;
            font-size: 18px;
            cursor: pointer;
          }
        }
      }
    }

    &--active {
      visibility: visible;
      transform: translateX(0%);
      opacity: 1;
      width: 70%;
    }

    &--inactive {
      visibility: hidden;
      transform: translateX(100%);
      opacity: 0;
      width: 0;
    }
  }

  &__hello-container {
    width: 30%;
    left: 70.5%;
    position: fixed;
    z-index: 2;
    transition: all 0.8s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10%;
    color: white;
    font-family: "Raleway", sans-serif;

    &--active {
      visibility: visible;
      transform: translateX(0%);
    }

    &--inactive {
      visibility: hidden;
      transform: translateX(-100%);
    }
  }

  &__colored-container {
    position: fixed;

    background: linear-gradient(to right, #6372ff 0%, #5ca9fb 100%);
    // background-color: $base-color;
    width: 30%;
    height: 100vh;
    z-index: 1;
    transition: transform 1s ease;

    &--left {
      transform: translateX(0px);
    }

    &--right {
      transform: translateX(235%);
    }
  }
}
